.Pokecard {
    /* border: 2px solid mediumseagreen; */
    width: 300px;
    padding: 10px;
    margin: 1rem;
    box-shadow: 7px 10px 12px -5px rgba(0, 0, 0, 0.56);
    text-align: center;
    background-color: #ffffff;
    border-radius: 3px;
}

.Pokecard-title {
	margin-bottom: 1rem;
	font-weight: bold;
	font-size: 1.5rem;
}


.Pokecard-image {
	display: flex;
	justify-content: center;
	align-items: center;
	height: 250px;
	background-color: #eceff1;
	border-radius: 3px;
	margin-bottom: 1rem;
}

.Pokecard-image img {
    height: 100%;
    width: 100%;
}

.Pokecard img {
	transition: all ease 500ms;
}

.Pokecard img:hover {
	transform: scale(1.25);
	opacity: 0.7;
}

.Pokecard-data {
    font-size: 80%;
}
